.list-default {
  margin: 0;
  padding: 0;
  list-style: none;
}
.container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 40px;
}
@font-face {
  font-family: "Galien";
  src: url("../fonts/Galien-Bold.woff2") format("woff2"), url("../fonts/Galien-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: block;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/gilroy-regular.woff2") format("woff2"), url("../fonts/gilroy-regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: block;
}
@font-face {
  font-family: "GilroyBold";
  src: url("../fonts/gilroy-regular.woff2") format("woff2"), url("../fonts/gilroy-regular.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: block;
}
html {
  box-sizing: border-box;
  background-color: #ffffff;
}
* {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  color: #ffffff;
}
address {
  font-style: normal;
}
.title {
  margin: 0;
}
.main-header__button {
  border: none;
  background: none;
  cursor: pointer;
}
.main-header__logo {
  display: block;
  width: 40px;
  height: 40px;
}
.main-header__wrapper {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 40px;
  display: flex;
  justify-content: space-between;
}
.main-header {
  padding-top: 30px;
}
.button {
  display: block;
  text-align: center;
  padding-top: 18px;
  width: 220px;
  min-height: 56px;
  border-radius: 10px;
  color: #ffffff;
}
.intro {
  padding-bottom: 190px;
  background-image: url("../img/intro-illustration.png"), linear-gradient(#ffffff 66.39%, rgba(152, 195, 232, 0.2) 100%);
  background-repeat: no-repeat;
  background-position: 78% 10%, 100% 100%;
  background-size: 45% 100%, 100% 100%;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .intro {
    background-image: url("../img/intro-illustration@2x.png");
  }
}
.intro__inner {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 40px;
  padding-top: 100px;
}
.intro__title {
  padding-bottom: 40px;
  color: #1F3F68;
  font-family: "Galien", "Arial", sans-serif;
  font-weight: 700;
  font-size: 64px;
  letter-spacing: 1.2px;
}
.intro__text {
  width: 35%;
  margin: 0;
  padding-bottom: 70px;
  color: #1F3F68;
  font-size: 16px;
  letter-spacing: 1.8px;
  opacity: 0.6;
}
.intro__button {
  box-shadow: 5px 10px 20px rgba(53, 110, 173, 0.2);
  background-color: #5A98D0;
  color: #ffffff;
}
.intro__button:hover {
  background-color: #2b578f;
}
.features {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 40px;
  padding-top: 190px;
  padding-bottom: 170px;
  display: flex;
  justify-content: space-between;
}
.features__title {
  display: inline-block;
  width: 30%;
  font-size: 36px;
  text-align: left;
  letter-spacing: 1.8px;
  color: #316099;
  font-weight: 700;
}
.features__item {
  margin: 0 50px 50px;
}
.features__item:first-of-type {
  margin-top: 270px;
}
.features__list {
  width: 1010px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  max-height: 1300px;
  flex-wrap: wrap;
  align-content: flex-end;
}
@keyframes arrow_move {
  100% {
    transform: translateX(10px);
  }
}
.feature {
  width: 380px;
  min-height: 525px;
  padding: 48px 50px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 5px 20px 50px rgba(16, 112, 177, 0.2);
  border-radius: 10px;
}
.feature::before {
  content: "";
  display: block;
  background-repeat: no-repeat;
  padding-bottom: 56px;
}
.feature:first-of-type::before {
  background-image: url("../img/feature-icon_2.png");
  width: 250px;
  height: 140px;
}
.feature:nth-of-type(2)::before {
  background-image: url("../img/feature-icon_1.png");
  width: 280px;
  height: 210px;
  margin-top: -78px;
}
.feature:last-of-type::before {
  background-image: url("../img/feature-icon_3.png");
  width: 225px;
  height: 140px;
}
.feature__title {
  width: 285px;
  font-weight: 700;
  padding-bottom: 16px;
  font-size: 20px;
  text-align: left;
  letter-spacing: 1.8px;
  color: #1F3F68;
}
.feature__description {
  width: 290px;
  margin: 0;
  padding-bottom: 32px;
  opacity: 0.6;
  font-size: 16px;
  color: #1F3F68;
  letter-spacing: 1.8px;
}
.feature__link {
  display: flex;
  color: #5A98D0;
  letter-spacing: 2px;
  font-weight: 700;
  align-self: flex-start;
}
.feature__link:hover {
  color: #1F3F68;
}
.feature__link:hover::after {
  animation: arrow_move 0.6s;
  animation-fill-mode: forwards;
}
.feature__link:focus {
  color: #1F3F68;
}
.feature__link::after {
  content: "";
  display: block;
  margin-left: 15px;
  width: 16px;
  height: 16px;
  background-image: url("../img/icon-arrow.svg");
}
.partners {
  background-color: #5A98D0;
  color: #ffffff;
  border-top-left-radius: 200px;
  padding-bottom: 125px;
}
.partners__wrapper {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 40px;
  max-width: 940px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  padding-top: 100px;
}
.partners__info {
  opacity: 0.6;
  width: 540px;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.05em;
}
.partners__title {
  letter-spacing: 0.05em;
  font-size: 36px;
  line-height: 46px;
  padding-right: 97px;
}
.partners__list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 940px;
  display: flex;
  flex-wrap: wrap;
  padding-top: 110px;
  outline: 11px solid #5a98d0;
  outline-offset: -3px;
}
.partners__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 235px;
  min-height: 128px;
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  border-top-color: transparent;
  border-left-color: transparent;
  text-align: center;
  font-size: 20px;
  line-height: 46px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  cursor: pointer;
}
.partners__item:last-of-type {
  background: rgba(31, 63, 104, 0.2);
}
.partners__item:hover {
  background-color: rgba(31, 63, 104, 0.2);
}
.partners__link {
  color: #f2f2f2;
}
.partners__link:focus {
  text-decoration: underline;
}
.main-footer {
  background-color: #5a98d0;
  color: #ffffff;
}
.main-footer__wrapper {
  margin-right: 96px;
  position: relative;
  background-color: #316099;
  border-top-right-radius: 200px;
  padding-bottom: 40px;
}
.main-footer__wrapper::after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 391px;
  height: 1px;
  width: 80%;
  background-color: rgba(255, 255, 255, 0.4);
}
.main-footer__inner {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 40px;
  max-width: 940px;
  padding-top: 91px;
}
.main-footer__title {
  font-size: 36px;
  line-height: 46px;
  letter-spacing: 0.05em;
}
.main-footer__title--small {
  font-size: 20px;
  line-height: 30px;
  padding-bottom: 32px;
  margin: 0;
}
.main-footer__content-wrapper {
  display: flex;
  justify-content: space-between;
  padding-bottom: 200px;
}
.main-footer__content {
  width: 54%;
}
.main-footer__text {
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.05em;
  color: rgba(255, 255, 255, 0.6);
}
.main-footer__button {
  background-color: #5A98D0;
  align-self: center;
}
.main-footer__button:hover {
  background-color: #82b1db;
}
.main-footer__button:focus {
  outline: none;
  background-color: #82b1db;
}
.main-footer__info {
  display: flex;
  padding-bottom: 64px;
}
.main-footer__navigation {
  display: flex;
}
.main-footer__contacts {
  width: 44%;
  padding-right: 100px;
}
.main-footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-footer__about-us {
  padding-right: 80px;
}
.main-footer__link {
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.05em;
}
.main-footer__link:hover {
  text-decoration: underline;
}
.main-footer__link:focus {
  outline-color: #68481f;
  text-decoration: underline;
}
.main-footer__list-item {
  padding-bottom: 16px;
}
.contacts__logo {
  display: inline-block;
  width: 40px;
  height: 40px;
  vertical-align: middle;
}
.contacts__logo-name {
  display: inline-block;
  vertical-align: middle;
  padding-left: 15px;
  font-size: 28px;
  line-height: 46px;
  letter-spacing: 0.02em;
}
.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-top: 40px;
}
.contacts__item {
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.05em;
  color: #ffffff;
  padding-bottom: 24px;
}
.contacts__item::before {
  display: inline-block;
  padding-right: 10px;
}
.contacts__item--address:hover {
  text-decoration: #ffffff;
  text-decoration-color: #ffffff;
}
.contacts__item--address::before {
  content: "A.";
  font-weight: 700;
}
.contacts__item--phone a:hover {
  text-decoration: underline;
}
.contacts__item--phone::before {
  content: "T.";
  font-weight: 700;
}
.contacts__item--email::before {
  content: "E.";
  font-weight: 700;
}
.copyright {
  color: rgba(255, 255, 255, 0.4);
  letter-spacing: 0.05em;
  font-size: 16px;
  line-height: 26px;
}
@keyframes show {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0.1;
  }
  20% {
    opacity: 0.2;
  }
  30% {
    opacity: 0.3;
  }
  40% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.5;
  }
  60% {
    opacity: 0.6;
  }
  70% {
    opacity: 0.7;
  }
  80% {
    opacity: 0.8;
  }
  90% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}
@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-10px);
  }
  20%,
  40%,
  60%,
  80% {
    transform: translateX(10px);
  }
}
.modal {
  position: fixed;
  top: calc(50% - 400px);
  left: calc(70% - 540px / 2);
  z-index: 20;
  width: 540px;
  min-height: 800px;
  padding: 56px;
  box-shadow: 5px 10px 50px rgba(16, 112, 177, 0.2);
  border-radius: 10px;
  background-color: #ffffff;
  display: none;
}
.modal--show {
  display: block;
  animation: show 0.4s;
}
.modal--error {
  animation: shake 0.6s;
}
.modal__title {
  color: #1F3F68;
  text-transform: uppercase;
  font-size: 20px;
  list-style: 30px;
  letter-spacing: 0.04em;
  padding-bottom: 48px;
}
.modal__form {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.modal__label {
  display: block;
  color: #1F3F68;
  opacity: 0.8;
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 0.04em;
  padding-bottom: 10px;
}
.modal__button {
  width: 100%;
  margin: 0 auto;
  margin-top: 72px;
  background: none;
  border: none;
  background-color: #5A98D0;
  padding-top: 0;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.04em;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
}
.modal__button:hover {
  background-color: #2b578f;
}
.modal__tooltip {
  font-weight: 700;
  display: none;
  font-size: 18px;
  color: #1F3F68;
  text-align: center;
}
.modal__input {
  font-size: 20px;
  opacity: 0.4;
  border: 1px solid #356ead;
  border-radius: 10px;
  width: 100%;
  height: 72px;
  padding-left: 42px;
}
.modal__input--message {
  height: 144px;
  resize: none;
  padding-top: 20px;
}
.modal__input:hover {
  border: solid 1px #1F3F68;
}
.modal__input:focus {
  outline: solid 2px #1F3F68;
}
.modal__input:focus::-webkit-input-placeholder {
  color: transparent;
}
.modal__input:focus::-moz-placeholder {
  color: transparent;
}
.modal__input:focus:-ms-input-placeholder {
  color: transparent;
}
.modal__input:invalid:not(:focus):not(:placeholder-shown) {
  border: 2px solid #ff8282;
}
.modal__input:valid:not(:focus):not(:placeholder-shown) {
  border: 2px solid lightgreen;
}
::-webkit-input-placeholder {
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 0.04em;
  color: #1f3f68;
  opacity: 0.2;
}
::-moz-placeholder {
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 0.04em;
  color: #1f3f68;
  opacity: 0.2;
}
:-ms-input-placeholder {
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 0.04em;
  color: #1f3f68;
  opacity: 0.2;
}
.modal__results {
  font-size: 22px;
  color: green;
  display: none;
}
.modal__results--open {
  display: block;
}
